*,
::after,
::before {
  box-sizing: border-box;
}

@import "../../scssVariables/variables.scss";

#registrationContainer {
  width: 100%;
  padding: 150px 0;
  background-color: $BG-color;
  font-family: monospace, serif;
  letter-spacing: 0.05em;
  box-sizing: border-box;

  #choice {
    width: 40%;
    height: 100px;
    margin: 0 auto;
    display: grid;
    gap: 60px;
    grid-template-columns: repeat(2, auto);
    justify-content: space-evenly;
    align-items: end;

    button {
      color: $color_texts;
      width: 100px;
      height: 50px;
      border: black;
    }
  }

  #signUpSection,
  #signInSection {
    width: 350px;
    max-width: 950px;
    min-width: 250px;
  }

  h1 {
    font-size: 23px;
  }

  .form {
    width: minmax(300px, 1fr);
    padding: 64px 0 24px;
    display: grid;
    place-items: center;

    .control {
      width: 100%;
      text-align: center;
      color: #fff;
      margin: 0 0 24px;

      input {
        width: 100%;
        padding: 14px 16px;
        border: 0;
        background: transparent;
        color: #fff;
        font-family: monospace, serif;
        letter-spacing: 0.05em;
        font-size: 16px;

        &:hover,
        &:focus {
          outline: none;
          border: 0;
        }
      }
    }

    .btn {
      width: 100%;
      display: block;
      padding: 14px 16px;
      background: transparent;
      outline: none;
      border: 0;
      color: #fff;
      letter-spacing: 0.1em;
      font-weight: bold;
      font-family: monospace;
      font-size: 16px;
    }
  }

  .block-cube {
    position: relative;
    cursor: pointer;

    .bg-top {
      position: absolute;
      height: 10px;
      background: rgb(2, 0, 36);
      background: $bg_gradient;
      bottom: 100%;
      left: 5px;
      right: -5px;
      transform: skew(-45deg, 0);
      margin: 0;

      .bg-inner {
        bottom: 0;
      }
    }

    .bg {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      background: rgb(2, 0, 36);
      background: $bg_gradient;
    }

    .bg-right {
      position: absolute;
      background: rgb(2, 0, 36);
      background: rgba(0, 212, 255, 1);
      top: -5px;
      z-index: 0;
      bottom: 5px;
      width: 10px;
      left: 100%;
      transform: skew(0, -45deg);

      .bg-inner {
        left: 0;
      }
    }

    .bg {
      .bg-inner {
        transition: all 0.2s ease-in-out;
      }
    }

    .bg-inner {
      background: $BG-color;
      position: absolute;
      left: 2px;
      top: 2px;
      right: 2px;
      bottom: 2px;
    }

    .text {
      position: relative;
      z-index: 2;
    }

    &.block-input {
      input {
        position: relative;
        z-index: 2;

        &:focus~.bg-right .bg-inner,
        &:focus~.bg-top .bg-inner,
        &:focus~.bg-inner .bg-inner {
          top: 100%;
          background: rgba(255, 255, 255, 0.5);
        }
      }

      .bg-top,
      .bg-right,
      .bg {
        background: rgba(255, 255, 255, 0.5);
        transition: background 0.2s ease-in-out;
      }

      .bg-right,
      .bg-top {
        .bg-inner {
          transition: all 0.2s ease-in-out;
        }
      }

      &:focus,
      &:hover {

        .bg-top,
        .bg-right,
        .bg {
          background: rgba(255, 255, 255, 0.8);
        }
      }
    }

    // State hover, focus
    &.block-cube-hover:focus,
    &.block-cube-hover:hover {
      .bg {
        .bg-inner {
          top: 100%;
        }
      }
    }
  }
}

@media screen and (max-width: 500px) {
  #registrationContainer {

    #signUpSection,
    #signInSection {
      width: 250px;
    }
  }
}